        h.push('<span>', this['_paletteHTML' + pIdx](), '</span>');
        h.push('<div class="evo-more"><a href="javascript:void(0)">', labels[1 + pIdx], '</a>');
        if (opts.history) {
            h.push('<a href="javascript:void(0)" class="evo-hist">', labels[5], '</a>');
        }
        h.push('</div>');
